.spinner {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: 0;
    visibility: hidden;
    display: flex;
    position: fixed;
    transition: visibility .7s, opacity 0.7s linear;
    max-width: 100%;
    min-height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 10;
}